$text-color: white;
$menu-background-color: #262626;
$menu-border-color: white;
$panel-opacity: 0.95;

html,
body {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
    font-family: sans-serif;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: #ccc; 
}

::-webkit-scrollbar-thumb {
    background: #888; 
}

::-webkit-scrollbar-thumb:hover {
    background: #555; 
}

#renderCanvas  {
    width: 100%;
    height: 100%;
}

.viewcenter{
    position: absolute;
    right: calc(50% - 2px);
    top: calc(50% - 2px);
    height: 4px;
    width: 4px;
    z-index: 10;
    background: red;
}

#info-panel {

    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    z-index: 10;
    background: $menu-background-color;
    border-left: 0px solid $menu-border-color;
    transition-property: width;
    transition-duration: 0.3s;
    opacity: $panel-opacity;

    #show-info {

        cursor: pointer;
        outline: none;
        position: absolute;
        right: 100%;
        top: 5px;
        border-radius: 10px 0 0 10px;
        border: 1px solid $menu-border-color;
        border-right: none;
        height: 40px;
        width: 25px;
        background: transparentize($menu-background-color, 0.7); 
        color: $text-color;
        font-weight: bold;
        font-family: monospace;
        font-size: 18px;

        &:hover {
            background: transparentize($menu-background-color, 0.5); 
        }

        &:active {
            font-size: 22px;
        }
    }

    .info-container{

        overflow-y: auto;
        height: 100%;

    }

    .elements-container{

        overflow-x: hidden;
        margin: 10px;
        padding: 10px;
        background: darken($menu-background-color, 5%);
        color: $text-color;

        .header {
            border-bottom: 1px solid $menu-border-color;
        }
        
        .list {

            .element-container {

                margin-top: 10px;
                margin-left: 10px;

                & > div:first-child {
                    margin-top: 0px;
                }
                
                & > div:not(:first-child) {
                    margin-top: 10px;
                }

            }
            
            .element {

                .element-name {

                    display: flex;
                    align-items: center;
                    margin-bottom: 4px;
                    
                    .display-button, .btn-suppr {

                        margin-right: 5px;
                        outline: none;
                        border-radius: 5px;
                        border: 1px solid #555;
                        background: none;
                        color: white;
                        padding-bottom: 3px;
                        cursor: pointer;

                        &:hover {
                            background: #555;
                        }

                    }

                    label {

                        padding-left: 5px;
                        margin-right: 5px;
                        flex-grow: 2;
                        overflow-x: hidden;
                        cursor: pointer;

                        &:hover {
                            background: #333;
                        }

                    }

                    input {
                        margin-right: 5px;
                        flex-grow: 2;
                    }

                }

                .element-params {

                    margin-left: 9px;
                    border-left: solid 1px white;
                    padding-left: 15px;
                    overflow-y: hidden;
                    transition-property: height;
                    transition-duration: 0.3s;

                    .param-img {

                        margin-top: 5px;
                        padding-top: 10px;
                        margin-bottom: 10px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: relative;

                        img {
                            width: 150px;
                            border: 1px solid white;
                        }

                        button {

                            position: absolute;
                            bottom: 7px;
                            border: solid 1px white;
                            color: rgba(255,255,255,0.8);
                            background: rgba(0,0,0,0.2);
                            border-radius: 5px;
                            outline: none;
                            cursor: pointer;

                            &:hover {
                                color: rgba(255,255,255,1);
                                background: rgba(0,0,0,0.5);
                            }

                        }

                    }

                    .param {

                        padding: 5px 0;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        label {

                        }

                        input {
                            width: 80px;
                        }

                    }

                }

            }

        }
        
        .footer {
            // border-bottom: 1px solid $menu-border-color;
        }

    }

}


.interactions-container{

    overflow-x: hidden;
    margin: 10px;
    padding: 10px;
    background: darken($menu-background-color, 5%);
    color: $text-color;

    .header {
        border-bottom: 1px solid $menu-border-color;
    }

    .editor-container {

        background: #fff;
        margin-top: 5px;

        #jsoneditor {

            height: 900px;

        }
        
    }

    .footer {

        margin-top: 5px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;

        .filler {
            flex: 2;
        }

        & > button:first-child {
            margin-top: 0px;
        }
        
        & > button:not(:first-child) {
            margin-left: 10px;
        }

        button {

            outline: none;
            border-radius: 5px;
            border: 1px solid $menu-border-color;
            background: $menu-background-color; 
            color: $text-color;
            cursor: pointer;

            &:hover {
                background: lighten($menu-background-color, 30); 
            }

            &:active {
                background: lighten($menu-background-color, 15); 
            }

        }
    }

}


.scene-DragAndDrop {

    opacity: 0.9;
    position: absolute;
    left: 10px;
    top: 10px;
    height: 100px;
    width: 100px;
    z-index: 10;
    border: 2px solid $menu-border-color;
    border-radius: 10px;
    overflow: hidden;
    color: white;
    $dnd-textshadow: #555;
    text-shadow: 2px 0 0 $dnd-textshadow, -2px 0 0 $dnd-textshadow, 0 2px 0 $dnd-textshadow, 0 -2px 0 $dnd-textshadow, 1px 1px $dnd-textshadow, -1px -1px 0 $dnd-textshadow, 1px -1px 0 $dnd-textshadow, -1px 1px 0 $dnd-textshadow;

    #sky-DragAndDrop {

        background-image: url("./img/sky.jpg");
        height: 50%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        &.dragOver{
            background-image: repeating-linear-gradient(
                -45deg,
                transparent,
                transparent 10px,
                transparentize(#fff, 0.4) 10px,
                transparentize(#fff, 0.4) 15px
            ),
            url("./img/sky.jpg");
        }

    }
    
    #ground-DragAndDrop {

        background-image: url("./img/ground.jpg");
        height: 50%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        &.dragOver{
            background-image: repeating-linear-gradient(
                -45deg,
                transparent,
                transparent 10px,
                transparentize(#fff, 0.4) 10px,
                transparentize(#fff, 0.4) 15px
            ),
            url("./img/ground.jpg");
        }
    }

}

#states-panel {

    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 15;
    background: $menu-background-color;
    border-top: 0px solid $menu-border-color;
    transition-property: height;
    transition-duration: 0.3s;
    opacity: $panel-opacity;

    #show-states {

        cursor: pointer;
        outline: none;
        position: absolute;
        bottom: 100%;
        left: 5px;
        border-radius: 10px 10px 0 0;
        border: 1px solid $menu-border-color;
        border-bottom: none;
        height: 30px;
        width: 140px;
        background: transparentize($menu-background-color, 0.7); 
        color: $text-color;
        font-weight: bold;
        font-family: monospace;
        font-size: 18px;

        &:hover {
            background: transparentize($menu-background-color, 0.5); 
        }

        &:active {
            font-size: 20px;
        }
    }

    #states_viewer_component{
        // overflow-y: auto;
        // height: 100%;
        height: 100%;

    }

    .states-container {

        padding: 10px;
        // background: red;
        display: flex;
        flex-direction: column;
        height: calc(100% - 20px);
        color: $text-color;

        .header {

            padding: 5px 10px;
            display: flex;
            align-items: center;
            background: darken($menu-background-color, 5%);
            // width: 500px;
            // height: 100px;

            label {
                color: $text-color;
            }
            
            & > button:first-of-type {
                margin-left: 20px;
            }
            
            & > button:not(:first-of-type) {
                margin-left: 10px;
            }

            select {
                margin-left: 20px;
                width: 150px;
            }

            button {

                outline: none;
                border-radius: 5px;
                border: 1px solid $menu-border-color;
                background: $menu-background-color; 
                color: $text-color;
                cursor: pointer;
                height: 25px;
                width: 25px;

                &:hover {
                    background: lighten($menu-background-color, 30); 
                }

                &:active {
                    background: lighten($menu-background-color, 15); 
                }

                &:disabled {
                    cursor: not-allowed;
                    background: lighten($menu-background-color, 15); 
                    color: #999;
                    border-color: #999;
                }

            }

            #btn-add-state{
                background-image: url(./img/icons/add-icon.svg);
                background-size: 15px 15px;
                background-repeat: no-repeat;
                background-position: center;
            }
            
            #btn-duplicate-state{
                background-image: url(./img/icons/duplicate-icon.svg);
                background-size: 15px 15px;
                background-repeat: no-repeat;
                background-position: center;
            }
            
            #btn-rename-state{
                background-image: url(./img/icons/rename-icon.svg);
                background-size: 15px 15px;
                background-repeat: no-repeat;
                background-position: center;
            }
            
            #btn-delete-state{
                background-image: url(./img/icons/delete-icon.svg);
                background-size: 15px 15px;
                background-repeat: no-repeat;
                background-position: center;
            }

            .separator {
                margin: 0 10px 0 20px;
                border-right: 3px double #fff;
                height: 10px;
            }

        }

        .body {

            padding: 10px;
            flex-grow: 2; 
            height: calc( 100% - 35px - 20px );
            background: darken($menu-background-color, 5%);
            display: flex;
            justify-content: space-around;
            // margin-top: 10px;
            // align-items: stretch;

            .elements-container {

                height: calc( 100% - 10px );
                margin: 0 10px;
                width: 600px;
                background: $menu-background-color;
                padding: 5px 10px;

                .head {

                    margin-bottom: 10px;
                    display: grid;
                    grid-template-columns: 100px 1fr 30px;
                    align-items: center;
                    justify-items: start;
                    margin-right: 17px;

                    label {
                        font-size: 18px;
                    }

                    .button-show-hide-elements{

                        justify-self: end;

                        &.show {
                            background-image: url(./img/icons/show-icon.svg);
                            background-size: 15px 15px;
                            background-repeat: no-repeat;
                            background-position: center;
                        }

                        &.hide {
                            background-image: url(./img/icons/hide-icon.svg);
                            background-size: 15px 15px;
                            background-repeat: no-repeat;
                            background-position: center;
                        }

                    }

                    .checkbox-elements-visibles {
                        transform: scale(1.5);
                        // margin: 0;
                        justify-self: end;
                    }
                    
                    button {

                        justify-self: end;
                        outline: none;
                        border-radius: 5px;
                        border: 1px solid $menu-border-color;
                        background: $menu-background-color; 
                        color: $text-color;
                        cursor: pointer;
                        height: 25px;
                        width: 25px;

                        &:hover {
                            background: lighten($menu-background-color, 30);
                        }

                        &:active {
                            background: lighten($menu-background-color, 15);
                        }

                    }

                }

                .list {

                    overflow-y: scroll;
                    height: calc( 100% - 21px - 10px );

                    & > div:first-child {
                        margin-top: 0px;
                    }
                    
                    & > div:not(:first-child) {
                        margin-top: 5px;
                    }

                    .element {

                        .element-name {

                            display:flex;
                            justify-content: space-between;
                            align-items: center;

                            .name {
                                padding: 2px 10px;
                                flex-grow: 2; 
                                display:flex;
                                justify-content: space-between;
                                background: lighten($menu-background-color, 5);
                                cursor: pointer;
                                
                                &:hover {
                                    background: lighten($menu-background-color, 10); 
                                }

                                &:active {
                                    background: lighten($menu-background-color, 15); 
                                }

                                .button-display-element {
                                    font-family: monospace;
                                }
                            }

                            .checkbox-element-visible {
                                cursor: pointer;
                                margin: 0 10px;
                                transform: scale(1.3);
                            }

                        }

                        .element-params {

                            overflow: hidden;

                            .params {

                                background: lighten($menu-background-color, 7); 
                                display: flex;
                                align-items: center;
                                padding: 10px;
                                margin-right: 33px;
                                height: 130px;

                                .param-img {

                                    height: 102px;
                                    width: 150px;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    
                                    img {
                                        height: 100px;
                                        border: 1px solid white;
                                    }

                                }

                                .variables {

                                    flex-grow: 2;
                                    height: 100%;
                                    margin-left: 10px;
                                    display: flex;
                                    align-items: center;

                                    & > div:first-child {
                                        margin-left: 0px;
                                    }
                                    
                                    & > div:not(:first-child) {
                                        margin-left: 10px;
                                    }

                                    .position-container {
                                        height: 100%;
                                    }

                                    .position-container.point{
                                        display: flex;
                                        flex-direction: column;

                                        #select-position-type {
                                            margin-bottom: 5px;
                                        }
                                    }

                                    .position-container.linear{

                                        display: grid;
                                        grid-template-areas: "select occ"
                                                             "w w";
                                        grid-template-columns: 1fr 1fr;
                                        // grid-template-rows: 1fr 1fr;
                                        justify-items: start;
                                        align-items: center;
                                        grid-column-gap: 5px;
                                        grid-row-gap: 5px;

                                        #select-position-type {

                                            grid-area: select;

                                        }

                                        .occurence {

                                            grid-area: occ;
                                            display: flex;
                                            align-items: center;

                                            input {
                                                margin-left: 5px;
                                                width: 40px;
                                                height: 18px;
                                            }

                                        }

                                        .pos-wrapper {

                                            grid-area: w;
                                            justify-self: stretch;
                                            display: flex;
                                            justify-content: space-around;

                                        }
                                    }
                                    
                                    .position-container.spread{
                                        display: flex;
                                        flex-direction: column;

                                        #select-position-type {
                                            margin-bottom: 5px;
                                        }

                                        .spread {
                                            
                                            height: 60px;
                                            width: 120px;
                                            display: grid;
                                            grid-template-areas: "to io"
                                                                "th ih";
                                            align-items: center;
                                            justify-items: stretch;
                                            
                                            input {
                                                justify-self: end;
                                                width: 50px;
                                                height: 18px;
                                            }

                                            .occurence {
                                                grid-area: to;
                                            }

                                            .name-position-h {
                                                grid-area: th;
                                            }

                                            .input-occurence {
                                                grid-area: io;
                                            }

                                            .input-position-h {
                                                grid-area: ih;
                                            }

                                        }

                                    }

                                    .position {

                                        height: 100%;
                                        width: 70px;
                                        display: grid;
                                        grid-template-areas: "t t"
                                                             "nx ix"
                                                             "ny iy"
                                                             "nz iz";
                                        align-items: center;
                                        justify-items: stretch;

                                        label {
                                        }

                                        input {
                                            justify-self: end;
                                            width: 40px;
                                            height: 18px;
                                        }

                                        .title {
                                            justify-self: start;
                                            grid-area: t;
                                        }

                                        .input-position-x{
                                            grid-area: ix;
                                        }

                                        .input-position-y{
                                            grid-area: iy;
                                        }

                                        .input-position-z{
                                            grid-area: iz;
                                        }

                                        .name-position-x{
                                            grid-area: nx;
                                        }

                                        .name-position-y{
                                            grid-area: ny;
                                        }

                                        .name-position-z{
                                            grid-area: nz;
                                        }

                                    }

                                    .variables-container {

                                        display: flex;
                                        align-items: center;
                                        flex-direction: column;
                                        width: 115px;
 
                                        & > div:first-child {
                                            margin-top: 0px;
                                        }
                                        
                                        & > div:not(:first-child) {
                                            margin-top: 5px;
                                        }

                                        .size {

                                            width: 100%;
                                            display: flex;
                                            align-items: center;
                                            justify-content: space-between;

                                            .input-size {
                                                width: 40px;
                                                height: 18px;
                                            }

                                        }

                                        .transition {
                                            
                                            width: 100%;
                                            display: flex;
                                            align-items: center;
                                            justify-content: space-between;

                                            input {
                                                cursor: pointer;
                                                transform: scale(1.3);
                                            }

                                        }
                                        
                                        .opacity {

                                            width: 100%;
                                            display: flex;
                                            align-items: center;
                                            justify-content: space-between;

                                            .input-alpha {
                                                width: 40px;
                                                height: 18px;
                                            }

                                        }

                                    }

                                }

                            }

                        }

                    }

                }
                

            }

            .transitions-container {

                height: calc( 100% - 10px );
                margin: 0 10px;
                width: 500px;
                background: $menu-background-color;
                padding: 5px 10px;

                .head {
                    font-size: 18px;
                    margin-bottom: 10px;
                }

                .list {

                    overflow-y: auto;
                    height: calc( 100% - 21px - 10px );
                    display: flex;
                    flex-direction: column;

                    & > div:first-child {
                        margin-top: 0px;
                    }
                    
                    & > div:not(:first-child) {
                        margin-top: 5px;
                    }

                    & > div:nth-child(2n){
                        background: lighten($menu-background-color, 10); 
                    }

                    & > div:nth-child(2n+1){
                        background: lighten($menu-background-color, 20); 
                    }

                    .transition, .transition-title {
                        display: grid;
                        grid-template-columns: 30px 100px 1fr 20px 1fr 30px;
                        align-items: center;
                        justify-items: stretch;
                        grid-column-gap: 10px;
                        padding: 5px;
                    }

                    #arrow {
                        justify-self: center;
                        background-image: url(./img/icons/right-arrow-icon.svg);
                        background-size: 15px 15px;
                        height: 15px;
                        width: 15px;
                        background-repeat: no-repeat;
                        background-position: center;
                    }
                    
                    button {

                        justify-self: end;
                        outline: none;
                        border-radius: 5px;
                        border: 1px solid $menu-border-color;
                        background: $menu-background-color; 
                        color: $text-color;
                        cursor: pointer;
                        height: 20px;
                        width: 20px;

                        &:hover {
                            background: lighten($menu-background-color, 30);
                        }

                        &:active {
                            background: lighten($menu-background-color, 15);
                        }

                    }

                }

            }

            .separator {
                margin: 0 10px;
                border-right: 1px solid #888;
                // height: 10px;
            }

        }

    }

    // .info-container{

    //     overflow-y: auto;
    //     height: 100%;

    // }


}